import * as React from 'react'
import { createAnimation } from "./helpers/animation";

export default function Border({children}) {
  
  const anim= createAnimation(
    "to-top",
    `100%{bottom: 200%}`,
    'linear'
  )

  const wrapper : React.CSSProperties ={
    marginInline: "auto",
    position: "relative",
    width: "200px",
    height: "500px",
    backgroundColor: "#171a36"
  }

  const cardline : React.CSSProperties ={
    
  }


  const cardLineLeft : React.CSSProperties={
    position: "absolute",
    animationFillMode: "fowards",
    animation: `${anim}`,
    background: "linear-gradient(to bottom, #2bd9e9, trasparent)",
    left: "0",
    bottom: "-100%",
    width: "5px",
    height: "100%"
  }

  const cardLineRight : React.CSSProperties={
    position: "absolute",
    animation: "1s 1s linear infinite",
    animationName: "to-bottom",
    background: "linear-gradient(to bottom, trasparent, #2bd9e9)",
    right: "0",
    top: "-100%",
    width: "5px",
    height: "100%"
  }

  const cardLineTop : React.CSSProperties={
    position: "absolute",
    animation: "1s 1s linear infinite",
    animationName: "to-right",
    background: "linear-gradient(to right, trasparent, #2bd9e9)",
    top: "0",
    width: "100%",
    height: "5px",
    left: "-100%"
  }

  const cardLineBottom : React.CSSProperties={
    position: "absolute",
    animation: "1s 1s linear infinite",
    animationName: "to-left",
    background: "linear-gradient(to right, #2bd9e9, transparent)",
    bottom: "0",
    right: "-100%",
    width: "100%",
    height: "5px"
  }
  return (
    <div style={wrapper}>
      <div aria-hidden="true" >
        <span style={cardLineLeft}></span>
        <span style={cardLineRight}></span>
        {children}
        <span style={cardLineTop}></span>
        <span style={cardLineBottom}></span>
      </div>
    </div>
  )
}
